import React, { Component } from 'react';
import UnityDescription from "../soft/unityDescription/UnityDescription";
import Service from "../soft/service/Service";
import Technology from "../soft/technology/Technology";
import PortfolioCompleted from "../soft/portfolioCompleted/PortfolioCompleted";
import PortfolioGames from "./portfolioGames/PortfolioGames";
import Publisher from "./publisher/Publisher";
import Event from "../soft/event/Event";
import RedesUnity from "../soft/redesUnity/RedesUnity"
import './Games.css';
import gamesData from 'data/unit/Games.json';

class Games extends Component {

  render() {
    return (
      <div className="Games">
        <UnityDescription unity={gamesData} />
        <p style={{ color: gamesData.colorTitles }} className="Games-title-services">SERVICES</p>
        <div className="Games-container-services">
          <div className="Games-container-background2-services">
            <img className="Games-background21-services" src={"https://drive.google.com/uc?id=14ML-upvcT3u2p35eKwUL6DIhgmyjzlHC"} alt={"background21"}></img>
          </div>
          {
            gamesData.services.map(
              (service, index) => {
                return <div key={index} className="Games-container-service"> 
                <Service 
                service={service} 
                color={gamesData.color} 
                colorTitle={gamesData.colorSubTitles} 
                marcoServices={gamesData.marcoServices} 
                /> </div>
              }
            )
          }
        </div>
        <p style={{ color: gamesData.colorTitles }} className="Games-title-services">TECHNOLOGIES <br />USED BY US</p>
        <div className="Games-container-allTechnologies">
          <div className="Games-container-background-technologies">
            <img className="Games-background-technologies" src={"https://drive.google.com/uc?id=1m2RjSkKXn5erJx_b972S7mgBdlVEBo8g"} alt={"backgroundTechnologies"}></img>
          </div>
          <div className="Games-container-technologies">
            <p style={{ color: gamesData.colorSubTitles }} className="Games-technology-title">ILUSTRATION</p>
            {
              gamesData.technologies.ilustration.map(
                (technology, index) => {
                  return <div key={index} className="Games-container-technology">
                  <Technology 
                  technology={technology} 
                  color={gamesData.color} 
                  /></div>
                }
              )
            }
          </div>
          <div className="Games-container-technologies">
            <p style={{ color: gamesData.colorSubTitles }} className="Games-technology-title">ANIMATION PROGRAMS</p>
            {
              gamesData.technologies.animation.map(
                (technology, index) => {
                  return <div key={index} className="Games-container-technology">
                  <Technology 
                  technology={technology} 
                  color={gamesData.color} 
                  /></div>
                }
              )
            }
          </div>
          <div className="Games-container-technologies">
            <p style={{ color: gamesData.colorSubTitles }} className="Games-technology-title">DEVELOPMENT ENGINES</p>
            {
              gamesData.technologies.development.map(
                (technology, index) => {
                  return <div key={index} className="Games-container-technology">
                  <Technology 
                  technology={technology} 
                  color={gamesData.color} 
                  /></div>
                }
              )
            }
          </div>
        </div>
        <p style={{ color: gamesData.colorTitles }} className="Games-title-services">PROJECTS</p>
        <p style={{ color: gamesData.colorSubTitles }} className="Games-technology-title">ONGOING</p>
         <div className="Games-container-portfolioCompleted">
          {
            gamesData.portfolio.completed.map(
              (portfolioCompleted, index) => {
                if(index === 0){
                return  <>
                        <div className="Games-container-background1-portfolioCompleted">
                          <img className="Games-background1-portfolioCompleted" src={"https://drive.google.com/uc?id=1t7KwSZdS2KizI9bVon-RL_lvwV5xPJ39"} alt={"backPortafolio1"}></img>
                        </div>
                        <img className="Games-background2-portfolioCompleted" src={"https://drive.google.com/uc?id=124mL9lUu2d-DbJTfvponggcOD12qDHc0"} alt={"backPortafolio2"}></img>
                        <p style={{ color: gamesData.colorTitles }} className="Games-completed-title">COMPLETED</p>
                        <PortfolioCompleted 
                        key={index} 
                        index={index} 
                        portfolioCompleted={portfolioCompleted} 
                        color={gamesData.color} 
                        marcoServices={gamesData.marcoServices}
                        />
                        </>
                }
                else{
                    return <PortfolioCompleted 
                    key={index} 
                    index={index} 
                    portfolioCompleted={portfolioCompleted} 
                    color={gamesData.color} 
                    marcoServices={gamesData.marcoServices}
                    />
                }
              }
            )
          }
        </div>
        <div className="Games-container-portfolioOngoing">
          {
            gamesData.portfolio.ongoing.map(
              (portfolioOngoing, index) => {
                return <PortfolioGames 
                key={index} 
                index={index} 
                portfolioOngoing={portfolioOngoing} 
                color={gamesData.color} 
                colorLink={gamesData.colorSubTitles} 
                marcoDesktop= {gamesData.marcoDesktop} 
                />
              }
            )
          }
        </div>
        <p style={{ color: gamesData.colorTitles }} className="Games-title-services">PUBLISHERS</p>
        <div className="Games-container-publishers">
          {
            gamesData.publishers.map(
              (publisher, index) => {
                return <Publisher 
                key={index} 
                publisher={publisher} 
                color={gamesData.color} 
                />
              }
            )
          }
        </div>
        <p style={{ color: gamesData.colorTitles }} className="Games-title-services">EVENTS</p>
        <div className="Games-container-events">
          {
            gamesData.events.map(
              (event, index) => {
                return <Event 
                key={index} 
                event={event} 
                color={gamesData.color} 
                marcoServices={gamesData.marcoServices}
                />
              }
            )
          }
        </div>
        <RedesUnity redes={gamesData.redes}/>
      </div>
    );
  }
}

export default Games;