import React, { Component } from 'react';
import './Publisher.css';



class Publisher extends Component {
  render() {
    return (
      <div className="Publisher">
        <div className="Publisher-container">
          <div className="Publisher-logoContainer">
            <img className="Publisher-logo" src={this.props.publisher.logo} alt={"SuperHippo"}></img>
            <img className="Publisher-background" src={this.props.publisher.background} alt={"SuperHippo"}></img>
          </div>
          <div className="Publisher-textContainer">
            <p style={{ color: this.props.color }} className="Publisher-description">
              {this.props.publisher.description}
            </p>
          </div>
        </div>
      </div>
    );
  }
}

export default Publisher;